<!-- 预生成对话框 -->
<template>
  <div class="main-pre-content"
       v-loading.fullscreen.lock="releaseLoading"
       element-loading-spinner="el-icon-loading"
       element-loading-text="发布正式版本">
    <el-row style="margin-bottom:10px">
      <el-col :span="6"
              :offset="18"
              style="text-align:right;">
        <el-button type="primary"
                   size="mini"
                   plain
                   :disabled="disabledState"
                   @click="releaseVersion">发布正式版本</el-button>
        <el-button type="primary"
                   plain
                   size="mini"
                   @click="downLoad">下载</el-button>
      </el-col>
    </el-row>
    <div ref="preTplPreview"
         v-loading="isLoading"
         element-loading-spinner="el-icon-loading"
         element-loading-text="模板加载中"
         class="review-content"
         :style="{height : (mainHeight - 120) + 'px'}"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Compile } from "@/assets/js/common";

export default {
  name: "MainEditPreGenerated",
  data() {
    return {
      isLoading: true,
      disabledState: false,
      releaseLoading: false
    };
  },
  props: {
    tplVersion: {
      required: true
    },
    sheetOrder: {
      required: true
    },
    prvd: {
      required: true
    },
    audTrm: {
      required: true
    }
  },
  computed: {
    ...mapState("fileModelConfig", {
      mainHeight: state => state.contentHeight, // 主体内容高度
      category: state => state.category, // 模板类别
      rowCode: state => state.editTplInfo.rowCode // 编辑行id
    })
  },
  methods: {
    // 加载预生成-预览文件
    loadPreTpl() {
      this.$refs.preTplPreview.scrollTop = 0;
      // 获取数据
      this.$api.fileModelConfig
        .getPreviewPreFile({
          rowCode: this.rowCode, // 行id
          category: this.category, // 模板版本类别
          versionId: this.tplVersion, // 选中模板id
          sheetOrder: this.sheetOrder // 选中报表位置id
        })
        .then(res => {
          // 编辑模板初始化预览
          this.isLoading = false;
          let EditTpl = Compile({
            template: `${res.preGenFile.txt}`,
            data() {
              return {
                style: res.preGenFile.style // DOM片段样式
              };
            }
          });
          // 将编译完的DOM片段插入DOM中
          this.$refs.preTplPreview.innerHTML = "";
          this.$refs.preTplPreview.appendChild(EditTpl);
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // 发布正式版本
    releaseVersion() {
      this.releaseLoading = true;
      this.$api.fileModelConfig
        .releaseVersion({
          rowCode: this.rowCode, // 行id
          category: this.category, // 模板版本类别
          versionId: this.tplVersion // 选中模板id
        })
        .then(res => {
          this.releaseLoading = false;
          if (res.setFullInfo) {
            this.disabledState = true;
            this.$message({
              type: "success",
              message: `正式版本发布成功`
            });
          } else {
            this.$alert("模板未完成编辑，不能发布正式版本", "提示", {
              confirmButtonText: "确定"
            });
          }
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // 下载
    downLoad() {
      window.open(
        `${this.$baseUrl}fileModelConfig/downPreGenFile?rowCode=${
          this.rowCode
        }&category=${this.category}&versionId=${this.tplVersion}&prvdId=${
          this.prvd
        }&audTrm=${this.audTrm}`
      );
    }
  },
  mounted() {
    this.loadPreTpl();
  }
};
</script>
