<!-- 主体内容-tab部分 -->
<template>
  <div class="main-content-tab">
    <el-tabs :style="{height : tabHeight + 'px'}"
             v-model="activeTab"
             tab-position="left"
             type="border-card"
             @tab-click="changeTab">
      <el-tab-pane name="Word">
        <span slot="label"><i class="iconfont icon-dq-text">&nbsp;</i>文本类模板</span>
        <!-- 主体内容 -->
        <tab-content v-if="WordTabVisiable"></tab-content>
      </el-tab-pane>
      <el-tab-pane name="Excel">
        <span slot="label"><i class="iconfont icon-baobiaobiaoweiguanli">&nbsp;</i>报表类模板</span>
        <!-- 主体内容 -->
        <tab-content v-if="ExcelTabVisiable"></tab-content>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import MainTabContent from "./MainTabContent";

export default {
  name: "MainTab",
  data() {
    return {
      // 当前活动tab页
      activeTab: "Word",
      WordTabVisiable: true,
      ExcelTabVisiable: false,
      // 模板类型下拉选项
      typeList: {
        // 文本类模板
        textTypes: [
          {
            id: "all",
            name: "全部"
          }
        ],
        // 报表类模板
        statementTypes: [
          {
            id: "all",
            name: "全部"
          }
        ]
      }
    };
  },
  computed: {
    // tab内容高度
    tabHeight() {
      return this.$store.state.fileModelConfig.contentHeight;
    }
  },
  components: {
    TabContent: MainTabContent
  },
  methods: {
    // 改变vuex状态
    ...mapActions("fileModelConfig", [
      "switchTab" // tab标签切换
    ]),
    // 标签页内容初始化
    initTab() {
      let tabName;
      if (this.activeTab === "Word") {
        this.WordTabVisiable = true;
        this.ExcelTabVisiable = false;
        tabName = "文本类模板";
      } else {
        this.ExcelTabVisiable = true;
        this.WordTabVisiable = false;
        tabName = "报表类模板";
      }
      this.switchTab({
        tabName: tabName,
        category: this.activeTab
      });
    },
    // 标签页切换
    changeTab(tab) {
      // 面包屑导航显示内容切换
      let tabName = tab.$slots.label[0].elm.innerText.substring(1);
      // 显示报表类模板tab内容，初始化界面的时候隐藏报表类模板tab内容，优化初次加载
      this.WordTabVisiable = true;
      this.ExcelTabVisiable = true;
      this.switchTab({
        tabName: tabName,
        category: this.activeTab
      });
    }
  },
  created() {
    // 数据初始化
    this.initTab();
  }
};
</script>

<style lang="scss" scoped>
.main-content-tab {
  /deep/ .el-tabs__header {
    .el-tabs__item {
      height: 90px;
      width: 130px;
      line-height: 90px;
    }
  }
}
</style>
