import Vue from 'vue';
import Vuex from 'vuex';
import fileModelConfig from './modules/fileModelConfig';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    roleId: '', // 登录角色ID
    xqglAuthStatus: '', // 需求管理权限
    sjbgysAuthStatus: '', // 审计报告预审权限
    rightControl: null, // 一经权限控制
  },
  mutations: {
    initRoleId(state, roleId) {
      state.roleId = roleId;
    },
    initXqglAuthStatus(state, roleId) {
      state.xqglAuthStatus = roleId;
    },
    initSjbgysAuthStatus(state, roleId) {
      state.sjbgysAuthStatus = roleId;
    },
    initRightControl(state, roleId) {
      state.rightControl = roleId;
    }
  },
  actions: {
    initRoleId(context, roleId) {
      context.commit('initRoleId', roleId);
    },
    initXqglAuthStatus(context, roleId) {
      context.commit('initXqglAuthStatus', roleId);
    },
    initSjbgysAuthStatus(context, roleId) {
      context.commit('initSjbgysAuthStatus', roleId);
    },
    initRightControl(context, roleId) {
      context.commit('initRightControl', roleId);
    }
  },
  modules: {
    fileModelConfig
  }
})