/**
 * 报告模板配置界面状态管理
 */
const fileModelConfigStore = {
  // 局部命名
  namespaced: true,
  state: {
    contentHeight: document.body.clientHeight - 127, // 主体内容高度
    category: '', // 模板类别
    activeTab: '', // 当前活动tab页
    subjectId: '', // 查询选项审计专题
    editContentVisible: false, // 报告模板配置-编辑页面显示状态
    editTplInfo: null, // tab界面-需要编辑模板信息-scope.row对象
    updateDialogVisible: false, // 更新配置-对话框显示状态
    editFormData: null, // 更新模板对话框-右侧编辑表单数据
    blockData: null, // 更新模板-编辑/复用按钮点击选中段落信息
    tplOperation: '', // 更新配置-对话框右侧操作界面状态:编辑edit/复用reuse
    reuseEditData: null, // 更新配置-复用段落配置数据
    sheetOrder: '', // 更新配置-报表位置
  },
  mutations: {
    // 主体内容高度根据视图更新
    heightResize(state, height) {
      state.contentHeight = height - 127;
    },
    // 切换tab标签
    switchTab(state, tabInfo) {
      state.activeTab = tabInfo.tabName;
      state.category = tabInfo.category;
    },
    // 更新查询选项审计专题选中项
    updateSubjectId(state, selectedId) {
      state.subjectId = selectedId;
    },
    // 编辑界面显示状态
    ShowMainEdit(state) {
      state.editContentVisible = true;
    },
    HideMainEdit(state) {
      state.editContentVisible = false;
    },
    // 更新配置-对话框显示状态
    showUpdateDialogVisible(state) {
      state.updateDialogVisible = true;
    },
    hideUpdateDialogVisible(state) {
      state.updateDialogVisible = false;
    },
    // 保存更新模板对话框-当前段落编辑状态初始化数据
    initEditFormData(state, formData) {
      state.editFormData = formData;
    },
    // 编辑模板版本数据信息
    updateEditTplInfo(state, rowData) {
      state.editTplInfo = rowData;
    },
    // 更新模板-编辑/复用按钮点击选中段落信息
    updateBlockData(state, blockData) {
      state.blockData = blockData;
    },
    // 更新配置-报表位置信息
    updateSheetOrder(state, sheetOrder) {
      state.sheetOrder = sheetOrder;
    },
    // 更新配置-对话框右侧操作界面-更新
    updateTplOperation(state, operation) {
      state.tplOperation = operation;
    },
    // 更新配置-对话框右侧编辑表单-复用段落配置请求到的数据初始化
    setReuseEditData(state, data) {
      state.reuseEditData = data;
    }
  },
  actions: {
    heightResize(context, height) {
      context.commit('heightResize', height);
    },
    switchTab(context, tabName) {
      context.commit('switchTab', tabName);
    },
    updateSubjectId(context, selectedId) {
      context.commit('updateSubjectId', selectedId);
    },
    initEditFormData(context, formData) {
      context.commit('initEditFormData', formData);
    },
    ShowMainEdit(context) {
      context.commit('ShowMainEdit');
    },
    HideMainEdit(context) {
      context.commit('HideMainEdit');
    },
    showUpdateDialogVisible(context) {
      context.commit('showUpdateDialogVisible');
    },
    hideUpdateDialogVisible(context) {
      context.commit('hideUpdateDialogVisible');
    },
    updateEditTplInfo(context, rowData) {
      context.commit('updateEditTplInfo', rowData);
    },
    updateSheetOrder(context, sheetOrder) {
      context.commit('updateSheetOrder', sheetOrder);
    },
    updateBlockData(context, blockData) {
      context.commit('updateBlockData', blockData);
    },
    updateTplOperation(context, operation) {
      context.commit('updateTplOperation', operation);
    },
    initReuseEditData(context, data) {
      context.commit('setReuseEditData', data);
    }
  }
}

export default fileModelConfigStore;