/**
 * 报告模板配置-api
 */

import axios from '../axios';
export default {
  // 查询界面
  // 查询-审计选题-下拉选项
  getSubjectList(params) {
    return axios.get("/fileModelConfig/getSubject");
  },
  // 查询-模板类型-下拉选项
  getTplTypeList(params) {
    return axios.get("/fileModelConfig/getModeltype", {
      params: params
    });
  },
  // 查询结果-表格数据
  getDetailTableData(params) {
    return axios.get("/fileModelConfig/getTableData", {
      params: params
    });
  },
  // 详情表格-模板版本-版本数据
  getTplVersionData(params) {
    return axios.get("/fileModelConfig/getVersionData", {
      params: params
    })
  },
  // 详情表格-操作-删除
  deleteTplVersion(params) {
    return axios.get("/fileModelConfig/deleteByVersion", {
      params: params
    });
  },
  // 详情表格-操作-设为默认版本
  setDefaultTplVersion(params) {
    return axios.get("/fileModelConfig/setDefaultVersion", {
      params: params
    });
  },
  // 导入-导入模板-模板文件
  getTplFileList(params) {
    return axios.get("/fileModelConfig/getFileName", {
      params: params
    });
  },

  // 编辑界面
  // 模板编辑-报表位置下拉选项
  getSheetList(params) {
    return axios.get('/fileModelConfig/getExcelSheetName', {
      params: params
    })
  },
  // 模板编辑-导出配置文件
  exportConfigFile(params) {
    return axios.get('/fileModelConfig/outPutConf', {
      params: params
    })
  },
  // 模板编辑-模板预览-html文件预览
  getPreviewTplFile(params) {
    return axios.get('/fileModelConfig/getModelTxtNoHandle', {
      params: params
    });
  },
  // 更新配置-更新模板-带编辑按钮预览模板-html字符串
  getEditPreviewFile(params) {
    return axios.get('/fileModelConfig/getModelTxtHandleOne', {
      params: params
    });
  },
  // 更新配置-更新模板-带下标预览模板-html字符串
  getReusePreviewFile(params) {
    return axios.get('/fileModelConfig/getModelTxtHandleTwo', {
      params: params
    });
  },
  // 更新配置-更新模板-编辑-段落编辑信息
  getEditFormData(params) {
    return axios.get('/fileModelConfig/getEditInfoData', {
      params: params
    });
  },
  // 更新配置-更新模板-编辑表单-保存
  postEditData(params) {
    return axios.post('/fileModelConfig/updateFileModel', params);
  },
  // 预生成-获取省份列表和审计月列表
  getPrvdAndAudTrm(params) {
    return axios.get('/fileModelConfig/getPreGenAudTrmAndPrvd', {
      params: params
    });
  },
  // 预生成-生成文件状态
  getPreFileState(params) {
    return axios.get('/fileModelConfig/preGenFile', {
      params: params
    });
  },
  // 预生成-预生成文件预览-html字符串
  getPreviewPreFile(params) {
    return axios.get('/fileModelConfig/getPreGenFile', {
      params: params
    });
  },
  // 预生成-发布正式版本
  releaseVersion(params) {
    return axios.get('/fileModelConfig/setFullVersion', {
      params: params
    });
  }
}