<!-- 更新配置-复用 -->
<template>
  <div class="reuse-container">
    <el-form size="mini"
             :inline="true"
             :model="reuseForm"
             label-width="70px">
      <el-row>
        <el-col :span="category === 'Excel' ? 8 : 10">
          <el-form-item label="模板版本">
            <el-select v-model="reuseForm.versionId">
              <el-option v-for="(item, index) in tplVersionList"
                         :key="index"
                         :label="item.name"
                         :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="category === 'Excel' ? 8 : 0"
                v-if="category === 'Excel'">
          <el-form-item label="报表位置"
                        v-if="category === 'Excel'">
            <el-select v-model="reuseForm.sheetOrder">
              <el-option v-for="(item, index) in sheetList"
                         :key="index"
                         :label="item.sheetName"
                         :value="item.sheetOrder">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="category === 'Excel' ? 8 : 10">
          <el-form-item label="段落位置">
            <el-select v-model="reuseForm.blockOrder">
              <el-option v-for="(item, index) in blockList"
                         :key="index"
                         :label="item.blockName"
                         :value="item.blockOrder">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div v-loading="isLoading"
         element-loading-spinner="el-icon-loading"
         element-loading-text="模板加载中"
         class="review-content"
         ref="reusePrview">
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { Compile } from "@/assets/js/common";

export default {
  name: "ReuseContent",
  data() {
    return {
      // 复用表单
      reuseForm: {
        // 模板版本选项
        versionId: "",
        // 报表位置选项
        sheetOrder: "",
        // 段落位置选项
        blockOrder: ""
      },
      // 报表位置选项
      sheetList: [
        {
          sheetOrder: 1,
          sheetName: "test"
        }
      ],
      // 段落位置选项
      blockList: [],
      // 模板加载状态
      isLoading: false
    };
  },
  computed: mapState("fileModelConfig", {
    category: state => state.category, // 模板类别
    rowCode: state => state.editTplInfo.rowCode, // 编辑行id
    tplVersionList: state => state.editTplInfo.versionList, // 模板版本下拉选项
    defaultTplVersion: state => state.editTplInfo.defaultVersionId, // 模板版本默认选中项-为该模板默认版本
    blockData: state => state.blockData // 编辑/复用段落-段落信息
  }),
  watch: {
    // 监听模板编辑-顶部模板版本选项变化，动态改变报表位置选项数据（报表类模板）/预览模板（文本类模板）
    "reuseForm.versionId"(newVal, oldVal) {
      if (this.category === "Excel") {
        this.$api.fileModelConfig
          .getSheetList({
            rowCode: this.rowCode,
            versionId: this.reuseForm.versionId
          })
          .then(res => {
            // 初始化
            this.sheetList = res.sheetNameData;
            this.reuseForm.sheetOrder = res.sheetNameData[0].sheetOrder;
            // 获取预览模板数据
            this.loadReuseTplPreview();
          });
      } else {
        // 获取预览模板数据
        this.loadReuseTplPreview();
      }
    },
    // 监听模板编辑-顶部报表位置选项变化，动态改变预览模板
    "reuseForm.sheetOrder"(newVal, oldVal) {
      // 判断oldVal是否为空，以防重复请求
      if (oldVal) {
        this.loadReuseTplPreview();
      }
    },
    // 监听reuseForm表单，如果选中报表位置，则跳转至编辑界面
    "reuseForm.blockOrder"(newVal, oldVal) {
      /**
       * 1. 显示编辑表单界面，将operation的值置为edit
       * 2. 请求编辑表单数据，进行编辑表单数据回填
       */
      this.$confirm(`确定复制该段落配置？`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          let postData = JSON.stringify(JSON.parse(this.reuseForm));
          postData.category = this.category;
          postData.rowCode = this.rowCode;
          this.$api.fileModelConfig
            .getEditFormData(postData)
            .then(res => {
              /* 
                需要等待编辑表单界面显示之后(表单数据加载初始状态)，才去更新表单数据，否则表单数据初始状态即为有数据的状态，从而导致表单无法重置
                使用async函数实现
              */
              let _this = this;
              async function updateEditFormSate() {
                await _this.updateTplOperation("edit");
                _this.initReuseEditData(res.editInfoData);
              }
              updateEditFormSate();
            })
            .catch(err => {
              this.$message.error(err);
            });
        })
        .catch(() => {});
    },
    // 监听选中段落信息变化
    blockData: {
      handler: function(newVal, oldVal) {
        if (newVal.blockOrder !== oldVal.blockOrder) {
          // 模板版本选中项初始化
          this.reuseForm.versionId = this.defaultTplVersion;
        }
      },
      deep: true
    }
  },
  methods: {
    // 改变vuex状态
    ...mapActions("fileModelConfig", [
      "initReuseEditData", // 更新复用段落配置信息状态
      "updateTplOperation" // 更新模板操作状态 编辑edit/复用reuse
    ]),
    // 加载模板预览
    loadReuseTplPreview() {
      this.isLoading = true;
      this.$refs.reusePrview.innerHTML = "";
      this.$nextTick(() => {
        // 获取数据
        this.$api.fileModelConfig
          .getReusePreviewFile({
            versionId: this.reuseForm.versionId, // 选中模板id
            sheetOrder: this.reuseForm.sheetOrder, // 选中报表位置id
            rowCode: this.rowCode, // 行id
            category: this.category // 模板版本类别
          })
          .then(res => {
            // 初始化
            this.$refs.reusePrview.scrollTop = 0;
            let tpl;
            if (res.txtHandleTwo.txt) {
              tpl = res.txtHandleTwo.txt;
            } else {
              tpl = `<p style="text-align:center">暂无数据</p>`;
            }
            /*
             动态编译html字符串
             将请求到的html字符串片段作为组件编译为DOM片段
             */
            let EditTpl = Compile({
              template: `${tpl}`,
              data() {
                return {
                  style: res.txtHandleTwo.style
                };
              }
            });
            // 初始化
            this.$refs.reusePrview.appendChild(EditTpl);
            this.reuseForm.blockOrder = "";
            this.blockList = res.txtHandleTwo.blockOrder;
            this.isLoading = false;
          })
          .catch(err => {
            this.$message.error(err);
          });
      });
    }
  },
  created() {
    // 模板版本选中项初始化
    this.reuseForm.versionId = this.defaultTplVersion;
  }
};
</script>

<style lang="scss" scoped>
.reuse-container {
  height: 100%;
  /deep/ .review-content {
    height: calc(100% - 50px);
  }
  /deep/ .el-form-item__content {
    width: 60%;
  }
}
</style>
