<!-- 更新配置-编辑表单 -->
<template>
  <div ref="editFormContainer" class="edit-form-container">
    <el-form ref="editForm" :model="editForm" label-width="120px" size="mini">
      <slot></slot>
      <el-form-item size="large" style="text-align:center">
        <el-button type="primary" size="small" @click="saveEdit">保存</el-button>
        <el-button size="small" @click="clearForm">清除</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "EditForm",
  data() {
    return {
      // 加载动画
      isLoading: false,
      // 表单信息
      editForm: this.formModel
    };
  },
  props: {
    // 版本id
    tplVersion: {
      required: true
    },
    // 表单数据
    formModel: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...mapState("fileModelConfig", {
      category: state => state.category, // 模板类别
      initEditData: state => state.editFormData, // 段落编辑表单数据，在保存&关闭窗口时进行校验
      rowCode: state => state.editTplInfo.rowCode, // 编辑行id
      blockData: state => state.blockData, // 编辑/复用段落-段落信息
      sheetOrder: state => state.sheetOrder // 报表位置信息
    }),
    // 序列化段落编辑表单数据，以便进行比对
    newEditDataString() {
      return JSON.stringify(this.editForm);
    }
  },
  methods: {
    // 改变vuex状态
    ...mapActions("fileModelConfig", [
      "initEditFormData" // 更新编辑表单数据状态
    ]),
    // 表单重置
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 编辑表单-保存
    saveEdit() {
      // 校验是否修改了表单内容
      if (this.initEditData != this.newEditDataString) {
        let postData = JSON.parse(JSON.stringify(this.editForm));
        postData.rowCode = this.rowCode;
        postData.blockOrder = this.blockData.blockOrder;
        postData.sheetOrder = this.sheetOrder;
        postData.category = this.category;
        postData.versionId = this.tplVersion;
        this.$api.fileModelConfig
          .postEditData(postData)
          .then(res => {
            this.$message({
              message: "已保存",
              type: "success"
            });
            // 更新表单旧数据
            this.initEditFormData(this.newEditDataString);
          })
          .catch(err => {
            this.$message.error(err);
          });
      } else {
        this.$message("表单内容未做更改，不需重复提交");
      }
    },
    // 编辑表单-清除提示
    clearForm() {
      this.$confirm("此操作不可恢复，确认清除？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.resetForm("editForm");
        })
        .catch(() => {});
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.editFormContainer.scrollTop = 0;
    });
  }
};
</script>

<style lang="scss" scoped>
.edit-form-container {
  height: 100%;
  padding: 0 10px 0 30px;
  overflow-y: auto;
  overflow-x: hidden;
}
</style>