import "babel-polyfill";
import Vue from 'vue';
import store from '@/store';
import api from '@/api';
import 'reset-css';
import 'style/common.scss';
import 'style/iconfont.css';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

// 基础路径
Vue.prototype.$baseUrl = process.env.BASE_URL;

// 请求-全局api
Vue.prototype.$api = api;

// 全局扩展器-用于动态构造组件
let Compile = (ComponentOptions) => {
  // Vue.extend构建的组件需要注册才能使用，类似于js中的createElement，创建但是不会添加。
  // 利用该特点可以实现动态的加载组件
  const Component = Vue.extend(ComponentOptions);
  // 实例化构造器,并进行挂载
  const mountedComponent = new Component().$mount();
  return mountedComponent.$el;
}

export {
  Vue,
  store,
  Compile
}