<template>
  <div class="main-tab-content">
    <!-- 顶部查询&导入表单 -->
    <el-row>
      <el-col :span="20">
        <el-form :inline="true"
                 :model="searchForm"
                 size="mini">
          <el-form-item label="审计专题">
            <el-select v-model="searchForm.subjectId"
                       name="subjectId">
              <el-option v-for="(item, index) in subjectList"
                         :key="index"
                         :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="模板类型">
            <el-select v-model="searchForm.modelTypeId"
                       placeholder="模板类型"
                       name="modelTypeId">
              <el-option v-for="(item, index) in tplTypeList"
                         :key="index"
                         :label="item.modelTypeName"
                         :value="item.modelTypeId"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <!-- 导入 -->
      <el-col :span="4"
              class="clearfix">
        <div class="float-r">
          <el-button type="primary"
                     size="mini"
                     @click="onSearch">查询</el-button>
          <el-button type="primary"
                     plain
                     size="mini"
                     @click="dialogFormVisible = true">导入</el-button>
        </div>
      </el-col>
    </el-row>
    <!-- 查询结果表格 -->
    <el-table v-loading="tableLoading"
              element-loading-spinner="el-icon-loading"
              element-loading-text="数据加载中"
              :data="tableData"
              :height="tableHeight"
              stripe
              style="width: 100%">
      <el-table-column prop="modelTypeName"
                       label="模板类型"
                       width="180"
                       align="center">
      </el-table-column>
      <el-table-column prop="versions"
                       label="模板版本"
                       width="180"
                       align="center">
        <template slot-scope="scope">
          <el-select v-model="scope.row.selected"
                     :name="scope.row.rowCode"
                     size="mini"
                     @change="switchVersion(scope)">
            <el-option v-for="(item, index) in scope.row.versionList"
                       :key="index"
                       :label="item.name"
                       :value="item.id"></el-option>
          </el-select>
        </template>
      </el-table-column>
      <el-table-column prop="isOfficial"
                       label="正式版本"
                       align="center">
        <template slot-scope="scope">
          <i class="color-primary iconfont icon-shi"
             v-if="scope.row.isOfficial"></i>
          <i class="color-primary iconfont icon-fou"
             v-else></i>
        </template>
      </el-table-column>
      <el-table-column prop="isDefault"
                       label="默认版本"
                       align="center">
        <template slot-scope="scope">
          <i class="color-primary iconfont icon-shi"
             v-if="scope.row.isDefault"></i>
          <i class="color-primary iconfont icon-fou"
             v-else></i>
        </template>
      </el-table-column>
      <el-table-column prop="reviser"
                       label="修改人"
                       align="center">
      </el-table-column>
      <el-table-column prop="revisionTime"
                       label="修改时间"
                       align="center">
      </el-table-column>
      <el-table-column prop="effectiveTime"
                       label="生效起始审计月"
                       align="center">
      </el-table-column>
      <el-table-column prop="operate"
                       label="操作"
                       width="240"
                       align="center">
        <template slot-scope="scope">
          <el-tooltip effect="dark"
                      content="编辑"
                      placement="top">
            <el-button type="warning"
                       icon="el-icon-edit"
                       size="mini"
                       @click="eidtVersion(scope)"
                       :disabled="!scope.row.selected"></el-button>
          </el-tooltip>
          <el-tooltip effect="dark"
                      content="删除"
                      placement="top">
            <el-button type="danger"
                       icon="el-icon-delete"
                       size="mini"
                       @click="deleteVersion(scope)"
                       :disabled="!scope.row.selected"></el-button>
          </el-tooltip>
          <el-tooltip effect="dark"
                      content="下载"
                      placement="top">
            <el-button type="success"
                       icon="el-icon-download"
                       size="mini"
                       @click="downloadTpl(scope)"
                       :disabled="!scope.row.selected"></el-button>
          </el-tooltip>
          <el-button type="primary"
                     size="mini"
                     @click="setDefault(scope)"
                     :disabled="Boolean(scope.row.isDefault) || !Boolean(scope.row.isOfficial) || !scope.row.selected">设为默认版本</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 导入模板对话框 -->
    <el-dialog width="30%"
               title="导入模板"
               :close-on-click-modal="false"
               :close-on-press-escape="false"
               @opened="resetForm('importForm')"
               :visible.sync="dialogFormVisible">
      <el-form ref="importForm"
               size="small"
               :model="importForm"
               label-width="70px">
        <el-form-item prop="subjectId"
                      label="审计专题">
          <el-select v-model="importForm.subjectId">
            <el-option v-for="(item, index) in subjectList"
                       :key="index"
                       :label="item.name"
                       :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="modelTypeId"
                      label="模板类型">
          <el-select v-model="importForm.modelTypeId"
                     :disabled="importTplTypeDisabled">
            <el-option v-for="(item, index) in importTplTypeList"
                       :key="index"
                       :label="item.modelTypeName"
                       :value="item.modelTypeId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="fileNameId"
                      label="模板文件">
          <el-select v-model="importForm.fileNameId"
                     :disabled="importTplFileListDisabled">
            <el-option v-for="(item, index) in importTplFileList"
                       :key="index"
                       :label="item.name"
                       :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer"
           style="text-align:center">
        <el-button size="small"
                   @click="dialogFormVisible = false">取 消</el-button>
        <el-button size="small"
                   type="primary"
                   @click.stop="confirmImport"
                   :disabled="importDisableState">确 认</el-button>
      </div>
    </el-dialog>
    <!-- 导入对话框 -->
    <el-dialog center
               v-if="dialogUploadVisible"
               :visible.sync="dialogUploadVisible"
               width="30%"
               title="请确认模板内容无误，否则需要重新导入"
               :close-on-click-modal="false"
               :close-on-press-escape="false"
               v-loading="isUploading"
               element-loading-spinner="el-icon-loading"
               element-loading-text="文件导入中">
      <div slot="footer"
           class="dialog-footer">
        <el-button size="small"
                   @click="dialogUploadVisible = false"
                   :disabled="isUploading">取 消</el-button>
        <!-- 文件导入 -->
        <el-upload class="upload"
                   action="/cmca/fileModelConfig/webUploader"
                   name="file"
                   :accept="category === 'Word' ? '.doc, .docx' : '.xlsx, .xls'"
                   :data="importData"
                   :show-file-list="false"
                   :before-upload="beforeUpload"
                   :on-success="importSuccess"
                   :on-error="importError">
          <el-button size="small"
                     type="primary"
                     :disabled="isUploading">确 定</el-button>
        </el-upload>
      </div>
    </el-dialog>
    <!-- 设为默认版本对话框 -->
    <el-dialog center
               width="30%"
               title="提示"
               :close-on-click-modal="false"
               :close-on-press-escape="false"
               :visible.sync="dialogSetDefaultVisible">
      <p class="demonstration">设为默认模板后，系统将根据此版本生成对应专题文件，您确定要把此版本设置为默认版本吗？</p>
      <div slot="footer"
           class="dialog-footer">
        <el-button size="small"
                   @click="dialogSetDefaultVisible = false">取 消</el-button>
        <el-button size="small"
                   type="primary"
                   @click="affirmSetDefault">确 认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "MainTabContent",
  data() {
    return {
      // 查询表单
      searchForm: {
        subjectId: "",
        modelTypeId: ""
      },
      // 导入表单
      importForm: {
        subjectId: "",
        modelTypeId: "",
        fileNameId: ""
      },
      // 审计专题下拉列表
      subjectList: [],
      // 模板类型下拉列表
      tplTypeList: [],
      // 表格数据
      tableData: null,
      // 表格数据加载等待动画
      tableLoading: true,
      // 导入模板-对话框状态
      dialogFormVisible: false,
      // 导入模板-对话框-模板类型下拉选项
      importTplTypeList: [],
      // 导入模板-对话框-模板类型下拉选项禁用状态
      importTplTypeDisabled: true,
      // 导入模板-模板文件下拉选项
      importTplFileList: [],
      // 导入模板-模板文件下拉选项禁用状态
      importTplFileListDisabled: true,
      // 导入模板-对话框确认按钮禁用状态
      importDisableState: true,
      // 导入-对话框状态
      dialogUploadVisible: false,
      // 导入-确认-等待动画
      isUploading: false,
      // 设为默认版本-对话框状态
      dialogSetDefaultVisible: false,
      // 设置默认版本-版本信息
      setDefaultVersionScope: null,
      // 导入文件-数据
      importData: {
        category: "",
        rowCodeVersionId: ""
      }
    };
  },
  computed: mapState("fileModelConfig", {
    tableHeight: state => state.contentHeight - 80, // tab内容高度
    category: state => state.category // 当前活动tab页模板类型
  }),
  watch: {
    // 查询表单-模板类型下拉选项根据审计专题数据联动
    "searchForm.subjectId": {
      handler: function(newVal, oldVal) {
        // 如果不是第一次切换，则重置模板类型选项
        if (oldVal) {
          this.$api.fileModelConfig
            .getTplTypeList({
              category: this.category,
              subjectId: this.searchForm.subjectId
            })
            .then(res => {
              res.modelType.unshift({
                modelTypeId: "all",
                modelTypeName: "全部"
              });
              this.searchForm.modelTypeId = "all";
              this.tplTypeList = res.modelType;
            })
            .catch(err => {
              this.$message.error(err);
            });
        }
      }
    },
    // 监听-导入模板-表单变化，改变确认按钮状态
    importForm: {
      handler: function(val, oldval) {
        if (val.subjectId && val.modelTypeId && val.fileNameId) {
          this.importDisableState = false;
        } else {
          this.importDisableState = true;
        }
      },
      deep: true
    },
    // 导入模板-表单-模板类型根据审计专题数据联动
    "importForm.subjectId": {
      handler: function(newVal, oldVal) {
        this.importTplTypeDisabled = true;
        this.importForm.modelTypeId = "";
        this.importTplTypeList = [];
        if (newVal) {
          this.$api.fileModelConfig
            .getTplTypeList({
              category: this.category,
              subjectId: this.importForm.subjectId
            })
            .then(res => {
              // 初始化
              this.importTplTypeDisabled = false;
              if (res.modelType.length > 0) {
                this.importTplTypeList = res.modelType;
                this.importForm.modelTypeId = res.modelType[0].modelTypeId;
              }
            })
            .catch(err => {
              this.$message.error(err);
            });
        }
      }
    },
    // 导入模板-表单-模板文件根据审计专题&模板类型数据联动
    "importForm.modelTypeId": {
      handler: function(newVal, oldVal) {
        this.importTplFileListDisabled = true;
        this.importForm.fileNameId = "";
        this.importTplFileList = [];
        if (newVal) {
          this.loadTplFileList();
        }
      }
    }
  },
  methods: {
    // 改变vuex状态
    ...mapActions("fileModelConfig", [
      "updateSubjectId", // 更新审计专题选中项状态
      "ShowMainEdit", // 显示模板编辑界面
      "updateEditTplInfo" // 更新编辑版本状态
    ]),
    // 表单重置功能
    resetForm(formName) {
      this.importTplTypeDisabled = true;
      this.importTplFileListDisabled = true;
      this.$refs[formName].resetFields();
    },
    // 顶部查询
    onSearch() {
      this.updateSubjectId(this.searchForm.subjectId);
      this.loadDetailTable();
    },
    // 查询结果表格
    loadDetailTable() {
      this.tableLoading = true;
      // 将版本类别添加到请求参数中
      let postData = JSON.parse(JSON.stringify(this.searchForm));
      postData.category = this.category;
      // 获取表格数据
      this.$api.fileModelConfig
        .getDetailTableData(postData)
        .then(res => {
          // 设置默认版本为下拉框默认选中项
          res.tableData.forEach((lineData, i) => {
            lineData.selected = lineData.defaultVersionId;
          });
          // 表格数据初始化
          this.tableData = res.tableData;
          this.tableLoading = false;
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    /**
     * tab内容初始化
     * tab组件内容初始化过程：获取审计专题下拉数据，设置默认选中项->根据模板类别+审计专题选中项->获取模板类型下拉选项->设置模板类型默认选中项->根据默认选中专题+默认选中模板类型->表格数据
     * 根据实现过程，需要使用同步执行的方法来层层获取数据，使用async+await实现Promise同步执行
     */
    async initTabContent() {
      // 获取查询-审计选题-下拉选项数据
      let subjectListData = await this.$api.fileModelConfig
        .getSubjectList()
        .catch(err => {
          this.$message.error(err);
        });
      // 审计专题下拉选项初始化
      this.subjectList = subjectListData.subjectList;
      this.searchForm.subjectId = 4;
      // 更新审计专题选中项
      this.updateSubjectId(this.searchForm.subjectId);
      // 获取查询-模板类型-下拉选项数据
      let tplTypeListData = await this.$api.fileModelConfig
        .getTplTypeList({
          category: this.category,
          subjectId: this.searchForm.subjectId
        })
        .catch(err => {
          this.$message.error(err);
        });
      // 模板类型下拉选项初始化
      tplTypeListData.modelType.unshift({
        modelTypeId: "all",
        modelTypeName: "全部"
      });
      this.tplTypeList = tplTypeListData.modelType;
      this.searchForm.modelTypeId = "all";
      // 渲染表格
      this.loadDetailTable();
    },
    /**
     * 版本切换
     * 用于 版本切换/删除版本后 更新数据
     * @param {object} scope scope对象 http://element-cn.eleme.io/#/zh-CN/component/table#table-column-scoped-slot
     * @param {boolean} isDefault 可选，默认false；是否切换到默认版本
     */
    switchVersion(scope, isDefault) {
      let selectedVersionId = isDefault
        ? scope.row.defaultVersionId
        : scope.row.selected; // 版本id
      // 获取版本数据
      this.$api.fileModelConfig
        .getTplVersionData({
          modelTypeId: this.searchForm.modelTypeId, // 当前选中模板类型id
          rowCode: scope.row.rowCode, // 当前操作行id
          versionId: selectedVersionId // 模板版本id
        })
        .then(res => {
          if (res.verisonData) {
            // 切换的版本数据
            let rowData = JSON.parse(JSON.stringify(res.verisonData));
            // 对本地已有的表格数据进行递归更新，利用数据双向绑定，更新视图
            for (const key in rowData) {
              if (rowData.hasOwnProperty(key)) {
                this.$set(this.tableData[scope.$index], key, rowData[key]);
              }
            }
          }
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    /**
     * 表格-编辑
     * @param {object} scope scope对象
     */
    eidtVersion(scope) {
      // 显示模板编辑界面
      this.ShowMainEdit();
      // 更新编辑版本状态
      this.updateEditTplInfo(scope.row);
    },
    /**
     * 表格-删除
     * 1. 先判断是否是默认版本，如果是默认版本提示不能删除
     * 2. 如果不是默认版本则可以删除，删除选中版本后，展示默认版本信息
     * @param {object} scope scope对象
     */
    deleteVersion(scope) {
      if (scope.row.isDefault) {
        this.$alert("该模板为默认版本，不能删除", "提示", {
          confirmButtonText: "确定"
        });
      } else {
        this.$confirm("您确认要删除该模板吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          // 向后台发送请求，删除数据库中对应的版本信息
          let postData = {
            rowCode: scope.row.rowCode,
            versionId: scope.row.selected
          };
          this.$api.fileModelConfig
            .deleteTplVersion(postData)
            .then(res => {
              if (res.deleteInfo) {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                // 更新本地当前行版本下拉列表数据，同步更新视图
                let nowVersionList = this.tableData[scope.$index].versionList;
                nowVersionList.forEach((version, idx) => {
                  // 移除选中的需要删除的版本
                  if (version.id === scope.row.selected) {
                    nowVersionList.splice(idx, 1);
                  }
                });
                // 更新本地选中项，显示默认版本信息
                scope.row.selected = scope.row.defaultVersionId;
                this.switchVersion(scope, true);
              } else {
                this.$message({
                  type: "error",
                  message: "删除失败!"
                });
              }
            })
            .catch(err => {
              this.$message.error(err);
            });
        });
      }
    },
    // 表格-下载
    downloadTpl(scope) {
      let rowCode = scope.row.rowCode;
      let versionId = scope.row.selected;
      let audTrm = scope.row.effectiveTime;
      window.open(
        `${
          this.$baseUrl
        }fileModelConfig/downLoadModelFile?rowCode=${rowCode}&versionId=${versionId}&category=${
          this.category
        }&audTrm=${audTrm.replace("-", "")}`
      );
    },
    // 表格-设为默认版本
    setDefault(scope) {
      // 判断是否为默认版本，如果不是默认版本则弹出提示对话框，并传入模板版本id
      if (!scope.row.isDefault) {
        this.dialogSetDefaultVisible = true;
        this.setDefaultVersionScope = scope; // 设置需要设置为默认版本的模板信息
      }
    },
    // 设为默认版本对话框-确认
    affirmSetDefault() {
      // 获取需要设置为默认版本的模板版本id
      let postData = {
        category: this.category,
        rowCode: this.setDefaultVersionScope.row.rowCode, // 行id
        versionId: this.setDefaultVersionScope.row.selected // 选中模板版本
      };
      this.$api.fileModelConfig
        .setDefaultTplVersion(postData)
        .then(res => {
          this.dialogSetDefaultVisible = false;
          // 设置默认版本成功后同步更新当前行显示信息
          this.switchVersion(this.setDefaultVersionScope);
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // 导入-导入模板对话框-模板文件
    loadTplFileList() {
      if (this.importForm.subjectId && this.importForm.modelTypeId) {
        // 获取数据
        this.$api.fileModelConfig
          .getTplFileList({
            category: this.category,
            subjectId: this.importForm.subjectId,
            modelTypeId: this.importForm.modelTypeId
          })
          .then(res => {
            // 初始化
            this.importTplFileListDisabled = false;
            if (res.fileList.length > 0) {
              this.importTplFileList = res.fileList;
              this.importForm.fileNameId = res.fileList[0].id;
            }
          })
          .catch(err => {
            this.$message.error(err);
          });
      } else {
        this.importTplFileList = null;
      }
    },
    // 导入-导入模板对话框-确认
    confirmImport() {
      setTimeout(() => {
        this.dialogFormVisible = false;
        this.dialogUploadVisible = true;
        this.importData.rowCodeVersionId = this.importForm.fileNameId;
        this.importData.category = this.category;
      }, 0);
    },
    // 导入对话框-上传状态-上传中
    beforeUpload(file) {
      this.isUploading = true;
      let isFileType;
      let errTips;
      switch (this.category) {
        case "Word":
          isFileType =
            file.type === "application/msword" ||
            file.type ===
              "application/vnd.openxmlformats-officedocument.wordprocessingml.document";
          errTips = "文本类模板只能导入.docx,.doc格式文件";
          break;
        case "Excel":
          isFileType =
            file.type === "application/vnd.ms-excel" ||
            file.type ===
              "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
          errTips = "报表类模板只能导入.xlsx,.xls格式文件";
          break;
      }
      if (!isFileType) {
        this.$alert(errTips, "文件格式错误", {
          confirmButtonText: "确定",
          callback: action => {
            this.isUploading = false;
            this.dialogFormVisible = true;
            this.dialogUploadVisible = false;
          }
        });
      }
      return isFileType;
    },
    // 导入-上传状态-上传成功
    importSuccess() {
      this.$message({
        type: "success",
        message: "导入成功!"
      });
      setTimeout(() => {
        // 更新表格内容
        this.loadDetailTable();
        this.isUploading = false;
        this.dialogUploadVisible = false;
      }, 0);
    },
    // 导入-上传状态-上传失败
    async importError(err) {
      this.$alert("导入失败，请重新导入", {
        confirmButtonText: "确定",
        callback: action => {
          this.isUploading = false;
          this.dialogFormVisible = true;
          this.dialogUploadVisible = false;
        }
      });
    }
  },
