<!-- 模板编辑-更新配置对话框组件 -->
<template>
  <el-dialog title="更新模板"
             :visible="updateDialogVisible"
             :close-on-click-modal="false"
             :close-on-press-escape="false"
             @open="openUpdateDialog"
             :before-close="closeTip"
             class="edit-dialog">
    <el-row :style="{height : (mainHeight - 90) + 'px'}">
      <!-- 预览部分 -->
      <el-col :span="isOperation ? 12 : 24"
              style="height:100%">
        <div ref="editTplPreview"
             v-loading="isUpdateTplLoading"
             element-loading-spinner="el-icon-loading"
             element-loading-text="模板加载中"
             class="review-content"></div>
      </el-col>
      <!-- 编辑/复用操作部分 -->
      <el-col v-if="isOperation"
              v-loading="operationlLoading"
              element-loading-spinner="el-icon-loading"
              element-loading-text="数据加载中"
              :span="isOperation ? 12 : 0"
              style="height:100%">
        <!-- 文本类-编辑表单 -->
        <edit-form ref="editForm"
                   v-if="operation === 'edit' && category === 'Word'"
                   :tpl-version="tplVersion"
                   :form-model="wordEditForm">
          <form-item-title iconColor="#FE9455">控制段落间合并</form-item-title>
          <el-form-item prop="mergeLast">
            <el-checkbox v-model="wordEditForm.mergeLast"
                         :true-label="1"
                         :false-label="0">与上一段落合并</el-checkbox>
          </el-form-item>
          <form-item-title iconColor="#AD54FF">控制该段落取值逻辑</form-item-title>
          <el-form-item prop="querySql"
                        label="SQL逻辑">
            <el-input v-model="wordEditForm.querySql"
                      type="textarea"
                      :rows="5"></el-input>
          </el-form-item>
          <form-item-title>控制该段落是否显示</form-item-title>
          <el-form-item prop="showType"
                        label="显示类型">
            <el-select v-model="wordEditForm.showType"
                       placeholder="模板类型">
              <el-option v-for="(item, index) in showTypeList"
                         :key="index"
                         :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <transition name="el-zoom-in-top">
            <el-form-item prop="showSql"
                          label="SQL逻辑"
                          v-show="wordEditForm.showType === 1">
              <el-input v-model="wordEditForm.showSql"
                        type="textarea"
                        :rows="5"></el-input>
            </el-form-item>
          </transition>
        </edit-form>
        <!-- 报表类-编辑表单 -->
        <edit-form ref="editForm"
                   v-if="operation === 'edit' && category === 'Excel'"
                   :tpl-version="tplVersion"
                   :form-model="excelEditForm">
          <form-item-title iconColor="#FE9455">控制报表类型</form-item-title>
          <el-row>
            <el-col :span="10">
              <el-form-item v-if="subjectId === 9"
                            prop="audTrmOption"
                            label="审计月参数">
                <el-select v-model="excelEditForm.audTrmOption">
                  <el-option v-for="(item, index) in audTrmOptionList"
                             :key="index"
                             :label="item.name"
                             :value="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="10">
              <el-form-item prop="isWideTb">
                <el-checkbox v-model="excelEditForm.isWideTb"
                             :true-label="1"
                             :false-label="0">循环多月数据</el-checkbox>
              </el-form-item>
            </el-col>
          </el-row>
          <transition name="el-zoom-in-top">
            <el-row v-if="excelEditForm.isWideTb">
              <el-col :span="10">
                <el-form-item prop="wideTbMonth"
                              label="起始月">
                  <el-input v-model="excelEditForm.wideTbMonth"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="10">
                <el-form-item prop="wideTbCol"
                              label="公共开始列">
                  <el-input v-model="excelEditForm.wideTbCol"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </transition>
          <form-item-title iconColor="#AD54FF">控制报表取数逻辑</form-item-title>
          <el-form-item prop="querySql"
                        label="SQL编辑">
            <el-input v-model="excelEditForm.querySql"
                      type="textarea"
                      :rows="5"></el-input>
          </el-form-item>
        </edit-form>
        <edit-reuse v-if="operation === 'reuse'"></edit-reuse>
      </el-col>
    </el-row>
  </el-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { Compile } from "@/assets/js/common";
import FormItemTitle from "./VFormTitle";
import EditReuse from "./MainEditUpdateDialogReuse";
import EditForm from "./MainEditUpdateDialogForm";

export default {
  name: "MainEditUpdateDialog",
  data() {
    return {
      // 更新配置-编辑模板-数据加载loading状态
      isUpdateTplLoading: true,
      // 更新配置-右侧-编辑栏显示状态
      isOperation: false,
      // 更新配置-右侧-编辑栏数据加载
      operationlLoading: true,
      // 更新配置-文本类-编辑栏表单
      wordEditForm: {
        querySql: "", // sql逻辑
        showSql: "", // 是否显示sql逻辑
        mergeLast: 0, // 与上一段落合并
        showType: 0 // 显示类型
      },
      // 更新配置-文本类-编辑栏显示类型下拉选项
      showTypeList: [
        {
          id: 0,
          name: "0-一律显示"
        },
        {
          id: 1,
          name: "1-SQL逻辑控制显示"
        }
      ],
      // 更新配置-报表类-编辑栏表单
      excelEditForm: {
        audTrmOption: "", // 审计月参数
        isWideTb: 0, // 循环多月数据
        wideTbMonth: "", // 起始月
        wideTbCol: "", // 公共开始列,
        querySql: "" // SQL逻辑
      },
      // 更新配置-报表类-审计月参数下拉选项
      audTrmOptionList: [
        {
          id: -1,
          name: "-1"
        },
        {
          id: -2,
          name: "-2"
        },
        {
          id: -3,
          name: "-3"
        }
      ]
    };
  },
  computed: {
    ...mapState("fileModelConfig", {
      mainHeight: state => state.contentHeight, // 主体内容高度
      subjectId: state => state.subjectId, // 查询表单审计专题选中项
      category: state => state.category, // 模板类别
      updateDialogVisible: state => state.updateDialogVisible, // 更新配置对话框显示状态
      rowCode: state => state.editTplInfo.rowCode, // 编辑行id
      operation: state => state.tplOperation, // 操作状态 编辑edit/复用reuse
      initEditData: state => state.editFormData, // 段落编辑表单数据，在保存&关闭窗口时进行校验
      blockData: state => state.blockData, // 编辑/复用段落-段落信息
      reuseEditData: state => state.reuseEditData // 复用段落配置-表单数据
    }),
    // 表单信息
    // TODO: 此处直接将对象赋值有问题，当修改数据的时候，会同时修改，现在的问题是第一次点开编辑之后点击复用再点击其他段落的编辑会导致清除按钮后，表单初始化到第一次编辑的数据状态
    editFormModel() {
      return this.category === "Word" ? this.wordEditForm : this.excelEditForm;
    },
    // 序列化段落编辑表单数据，以便进行比对
    newEditDataString() {
      return JSON.stringify(this.editFormModel);
    }
  },
  props: {
    tplVersion: {
      required: true
    },
    sheetOrder: {
      required: true
    }
  },
  watch: {
    // 监听更新配置对话框状态，关闭的时候进行初始化操作
    updateDialogVisible(newVal, oldVal) {
      if (!newVal) {
        // 对话框模板预览部分为空
        this.$refs.editTplPreview.innerHTML = "";
        // 关闭对话框右侧操作部分
        this.isOperation = false;
        // 编辑表单初始化
        this.initEditFormData(null);
      }
    },
    // 监听编辑段落切换，更新当前编辑段落表单信息
    blockData: {
      handler: function(newVal, oldVal) {
        this.initEditForm();
      },
      deep: true
    },
    // 监听请求到的复用段落配置的数据，对编辑表单进行数据回填
    reuseEditData: {
      handler: function(newVal, oldVal) {
        // 表单数据回填
        for (const key in this.editFormModel) {
          if (
            this.editFormModel.hasOwnProperty(key) &&
            newVal.hasOwnProperty(key)
          ) {
            this.editFormModel[key] = newVal[key];
          }
        }
      },
      deep: true
    }
  },
  components: {
    FormItemTitle,
    EditForm,
    EditReuse
  },
  methods: {
    // 改变vuex状态
    ...mapActions("fileModelConfig", [
      "hideUpdateDialogVisible", // 关闭更新模板对话框
      "updateTplOperation", // 更新模板操作状态 编辑edit/复用reuse
      "initEditFormData", // 更新编辑表单状态
      "updateBlockData" // 编辑/复用段落切换-更新段落信息
    ]),
    // 编辑状态保存提示-通用
    editSaveTip() {
      return this.$confirm("配置内容未保存，是否放弃", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      });
    },
    // 请求带编辑&复用按钮的html字符串，并编译为DOM片段，添加事件
    loadEditPreviewFile() {
      this.$refs.editTplPreview.scrollTop = 0;
      // 获取数据
      this.$api.fileModelConfig
        .getEditPreviewFile({
          versionId: this.tplVersion, // 选中模板id
          rowCode: this.rowCode, // 行id
          category: this.category, // 模板版本类别
          sheetOrder: this.sheetOrder // 选中报表位置id
        })
        .then(res => {
          // 编辑模板初始化预览
          this.isUpdateTplLoading = false;
          let _this = this;
          /**
           * 动态编译html字符串
           * 将请求到的html字符串片段作为组件编译为DOM片段，这样可以操作后台返回的html字符串中的元素
           */
          let EditTpl = Compile({
            template: `${res.txtHandleOne.txt}`,
            data() {
              return {
                style: res.txtHandleOne.style, // DOM片段样式
                blockInfo: null
              };
            },
            methods: {
              /**
               * 编辑/复用-段落切换
               * 1. 判断段落是否开始编辑：
               * 1.1 如果未开始编辑，直接修改段落信息为当前段落；
               * 1.2 如果开始编辑，判断段落是否切换：
               * 1.2.1 如果未切换段落：
               * 1.2.1.1 判断是否是同一操作(编辑/复用)，是同一操作不做任何处理；如果不是同一操作，改变操作类型，跳转至对应的操作界面；
               * 1.2.2 如果切换段落：
               * 1.2.2.1 判断表单是否已经编辑：
               * 1.2.2.1.1 如果修改，给出未保存提示，是否取消；如果取消，则继续编辑，不修改当前编辑段落信息；如果确认，则放弃编辑，修改当前编辑段落信息
               * 1.2.2.1.2 如果未修改，判断是否是同一操作(编辑/复用)，是同一操作不做任何处理；如果不是同一操作，改变操作类型，跳转至对应的操作界面；
               * 2. 之后，通过监听当前编辑段落信息，如果变化，就请求表单数据，进行数据回填
               */
              switchBlock(blockData, operation) {
                this.blockInfo = {
                  blockOrder: blockData[0],
                  sheetOrder: blockData[1]
                };
                if (_this.isOperation) {
                  if (_this.blockData.blockOrder !== blockData[0]) {
                    if (_this.initEditData !== _this.newEditDataString) {
                      _this
                        .editSaveTip()
                        .then(() => {
                          _this.updateBlockData(this.blockInfo);
                          if (operation !== _this.operation) {
                            _this.updateTplOperation(operation);
                          }
                        })
                        .catch(() => {});
                    } else {
                      _this.updateBlockData(this.blockInfo);
                      if (operation !== _this.operation) {
                        _this.updateTplOperation(operation);
                      }
                    }
                  } else {
                    if (operation !== _this.operation) {
                      _this.updateTplOperation(operation);
                    }
                  }
                } else {
                  _this.isOperation = true;
                  _this.updateBlockData(this.blockInfo);
                  _this.updateTplOperation(operation);
                }
                // if (_this.initEditData) {
                //   if (_this.initEditData !== _this.newEditDataString) {
                //     if (_this.blockData.blockOrder !== blockData[0]) {
                //       _this
                //         .editSaveTip()
                //         .then(() => {
                //           _this.updateTplOperation(operati~on);
                //           _this.updateBlockData(this.blockInfo);
                //         })
                //         .catch(() => {});
                //     } else {
                //       _this.updateTplOperation(operation);
                //     }
                //   } else {
                //     _this.updateTplOperation(operation);
                //     if (_this.blockData.blockOrder !== blockData[0]) {
                //       _this.updateBlockData(this.blockInfo);
                //     }
                //   }
                // } else {
                //   _this.updateBlockData(this.blockInfo);
                //   _this.updateTplOperation(operation);
                // }
              },
              // 编辑按钮
              edit(...blockData) {
                this.switchBlock(blockData, "edit");
              },
              // 复用按钮
              reuse(...blockData) {
                this.switchBlock(blockData, "reuse");
              }
            }
          });
          // 将编译完的DOM片段插入DOM中
          this.$refs.editTplPreview.innerHTML = "";
          this.$refs.editTplPreview.appendChild(EditTpl);
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // 更新配置-打开更新模板对话框
    openUpdateDialog() {
      // 显示更新模板对话框及loading状态
      this.isUpdateTplLoading = true;
      // 隐藏更新模板对话框右侧编辑表单
      this.isOperation = false;
      // 更新模板对话框组件DOM渲染完毕再进行模板内容组件填充操作
      this.$nextTick(() => {
        this.loadEditPreviewFile();
      });
    },
    // 更新配置-对话框关闭前-判断编辑表单保存状态
    async closeTip(done) {
      let state = true;
      if (this.isOperation && this.operation === "edit") {
        if (this.initEditData !== this.newEditDataString) {
          state = await this.editSaveTip()
            .then(() => {
              return true;
            })
            .catch(() => {
              return false;
            });
        }
      }
      if (state) {
        this.hideUpdateDialogVisible();
      }
    },
    // 更新配置-更新模板对话框-模板编辑-加载编辑表单信息
    initEditForm() {
      this.operationlLoading = true;
      // 获取数据
      this.$api.fileModelConfig
        .getEditFormData({
          category: this.category, // 模板类别
          rowCode: this.rowCode, // 行id
          versionId: this.tplVersion, // 选中模板id
          blockOrder: this.blockData.blockOrder, // 段落id
          sheetOrder: this.sheetOrder // sheet页id
        })
        .then(res => {
          let formData;
          if (res.editInfoData) {
            formData = res.editInfoData;
          } else {
            formData = {
              querySql: "", // sql逻辑
              showSql: "", // 是否显示sql逻辑
              mergeLast: 0, // 与上一段落合并
              showType: 0, // 显示类型
              audTrmOption: "", // 审计月参数
              isWideTb: 0, // 循环多月数据
              wideTbMonth: "", // 起始月
              wideTbCol: "", // 公共开始列,
              querySql: "" // SQL逻辑
            };
          }
          /*
            编辑表单数据初始化，需要更新表单初始值，以便在表单编辑后，进行编辑前后比较，方便保存提示
            编辑表单数据初始状态更新又分两种情况：
            1. 在编辑状态下，需要对表单数据进行回填，以进行展示
            2. 在复用状态下，点击复用后，展示复用界面，而不需要展示编辑表单界面，不用进行数据回填的操作
          */
          let initEditFormData = {};
          if (this.editFormModel) {
            if (this.operation === "edit") {
              for (const key in this.editFormModel) {
                if (
                  this.editFormModel.hasOwnProperty(key) &&
                  formData.hasOwnProperty(key)
                ) {
                  this.editFormModel[key] = formData[key];
                }
              }
              initEditFormData = this.editFormModel;
            } else {
              for (const key in this.editFormModel) {
                if (
                  this.editFormModel.hasOwnProperty(key) &&
                  formData.hasOwnProperty(key)
                ) {
                  initEditFormData[key] = formData[key];
                }
              }
            }
            this.initEditFormData(JSON.stringify(initEditFormData));
            this.operationlLoading = false;
          }
        })
        .catch(err => {
          this.$message.error(err);
        });
    }
  }
};
</script>
