<template>
  <the-layout>
    <div slot="main-container">
      <!-- 面包屑导航 -->
      <el-breadcrumb separator="\">
        <el-breadcrumb-item class="breadcrumb-active">报告模板配置</el-breadcrumb-item>
        <el-breadcrumb-item :class="{'breadcrumb-active':editContentVisible} "
                            @click.native="hideEditContent">
          {{ activeTabName }}
        </el-breadcrumb-item>
        <el-breadcrumb-item v-if="editContentVisible">模板编辑</el-breadcrumb-item>
      </el-breadcrumb>
      <!-- 主体内容 -->
      <el-card class="main-body">
        <!-- 主体内容-tab部分 -->
        <main-tab v-show="!editContentVisible"></main-tab>
        <!-- 主体内容-模板编辑部分 -->
        <main-edit v-if="editContentVisible"></main-edit>
      </el-card>
    </div>
  </the-layout>
</template>

<script>
import { mapState, mapActions } from "vuex";
import TheLayout from "component/TheLayout";
import MainTab from "./components/MainTab";
import MainEdit from "./components/MainEdit";

export default {
  name: "App",
  data() {
    return {};
  },
  computed: mapState("fileModelConfig", {
    activeTabName: state => state.activeTab, // 当前活动tab页
    editContentVisible: state => state.editContentVisible // 主体内容-编辑部分显示状态
  }),
  components: {
    TheLayout,
    MainTab,
    MainEdit
  },
  methods: {
    // 改变vuex状态
    ...mapActions("fileModelConfig", [
      "HideMainEdit", // 改变模板编辑界面状态
      "heightResize" // 自适应主体内容高度状态
      ]),
    // 隐藏编辑页面
    hideEditContent() {
      // 显示状态初始化，优化加载
      this.HideMainEdit();
    }
  },
  mounted() {
    // 主体内容高度初始化，并监听浏览器窗口变化，实时更新
    this.heightResize(document.body.clientHeight);
    // 监听 window 的 resize 事件．在浏览器窗口变化时再设置下区域高度．
    const _this = this;
    window.onresize = () => {
      _this.heightResize(document.body.clientHeight);
    };
  }
};
</script>

<style lang="scss" scoped>
@import "style/color-theme.scss";
.main-container {
  .main-body {
    margin-top: 16px;
    /deep/ .el-card__body {
      padding: 0;
    }
  }
}
</style>
