<!-- 主体内容-模板编辑部分 -->
<template>
  <!-- 编辑界面 -->
  <div class="main-edit-content">
    <!-- 顶部筛选&功能按钮 -->
    <el-row>
      <!-- 左侧表单 -->
      <el-col :span="14">
        <el-form :inline="true"
                 size="mini">
          <el-form-item label="模板版本">
            <el-select v-model="selectedTplVersion">
              <el-option v-for="(item, index) in tplVersionList"
                         :key="index"
                         :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="报表位置"
                        v-if="category === 'Excel'">
            <el-select v-model="selectedExcelSheet">
              <el-option v-for="(item, index) in sheetList"
                         :key="index"
                         :label="item.sheetName"
                         :value="item.sheetOrder"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <!-- 右侧功能按钮组 -->
      <el-col :span="10"
              class="clearfix">
        <div class="float-r">
          <el-button type="primary"
                     plain
                     size="mini"
                     @click="openUpdateDialog">更新配置</el-button>
          <el-button type="primary"
                     plain
                     size="mini"
                     @click="showPreFormDialog">预生成</el-button>
          <el-button type="primary"
                     plain
                     size="mini"
                     @click="exportConfigFile">导出配置文件</el-button>
          <el-upload class="upload"
                     v-loading.fullscreen.lock="isUploading"
                     action="/cmca/fileModelConfig/inputConf"
                     accept=".sql"
                     :data="importData"
                     :show-file-list="false"
                     :before-upload="uploading"
                     :on-success="importSuccess"
                     :on-error="importError">
            <el-button type="primary"
                       plain
                       size="mini"
                       @click="chooseFile">导入配置文件</el-button>
          </el-upload>
        </div>
      </el-col>
    </el-row>
    <!-- 模板编辑-预览 -->
    <div v-loading="isTplPreviewLoading"
         element-loading-spinner="el-icon-loading"
         element-loading-text="模板加载中"
         :style="{height : (mainHeight - 80) + 'px'}">
      <div class="review-content"
           ref="initPreview"></div>
    </div>
    <!-- 更新配置-对话框 -->
    <update-dialog ref="editDialog"
                   :tpl-version="selectedTplVersion"
                   :sheet-order="selectedExcelSheet"></update-dialog>
    <!-- 预生成-对话框 -->
    <el-dialog title="预生成"
               :visible.sync="preDialogVisible"
               :close-on-click-modal="false"
               :close-on-press-escape="false"
               :width="preTplVisible ? '90%' : '30%'">
      <div v-if="!preTplVisible">
        <el-form ref="preForm"
                 size="small"
                 :model="preForm"
                 label-width="70px">
          <el-form-item prop="prvdId"
                        label="省 份">
            <el-select v-model="preForm.prvdId">
              <el-option v-for="(item, index) in prvdList"
                         :key="index"
                         :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="audTrm"
                        label="审计月">
            <el-select v-model="preForm.audTrm">
              <el-option v-for="(item, index) in audTrmList"
                         :key="index"
                         :label="item.name"
                         :value="item.id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer"
             style="text-align:center">
          <el-button size="small"
                     @click="preDialogVisible = false">取 消</el-button>
          <el-button size="small"
                     type="primary"
                     @click="showPreTpl"
                     :disabled="preForm.prvdId == '' && preForm.audTrm == ''">确 认</el-button>
        </div>
      </div>
      <pre-content v-if="preTplVisible"
                   :tpl-version="selectedTplVersion"
                   :sheet-order="selectedExcelSheet"
                   :prvd="preForm.prvdId"
                   :audTrm="preForm.audTrm"></pre-content>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { Compile } from "@/assets/js/common";
import UpdateDialog from "./MainEditUpdateDialog";
import PreContent from "./MainEditPreGenerated";

export default {
  name: "MainEdit",
  data() {
    return {
      // 模板版本-选中项
      selectedTplVersion: "",
      // 报表位置（报表类模板）-选中项
      selectedExcelSheet: "",
      // 报表位置-下拉选项
      sheetList: [],
      // 导入配置文件-加载动画
      isUploading: false,
      // 预览模板文件-数据加载loading状态
      isTplPreviewLoading: true,
      // 预生成-对话框显示状态
      preDialogVisible: false,
      // 导入配置文件数据
      importData: {},
      // 预生成
      preTplVisible: false,
      preForm: {
        prvdId: "",
        audTrm: ""
      },
      prvdList: [],
      audTrmList: []
    };
  },
  computed: mapState("fileModelConfig", {
    mainHeight: state => state.contentHeight, // 主体内容高度
    subjectId: state => state.subjectId, // 专题id
    rowCode: state => state.editTplInfo.rowCode, // 编辑行id
    tplVersionList: state => state.editTplInfo.versionList, // 模板版本下拉选项
    editTplList: state => state.editTplInfo, // 获取模板版本下拉数据信息
    category: state => state.category // 获取模板类别
  }),
  watch: {
    // 监听模板编辑-顶部模板版本选项变化，动态改变报表位置选项数据（报表类模板）/预览模板（文本类模板）
    selectedTplVersion(newVal, oldVal) {
      if (this.category === "Excel") {
        this.$api.fileModelConfig
          .getSheetList({
            rowCode: this.rowCode,
            versionId: this.selectedTplVersion
          })
          .then(res => {
            // 初始化
            this.sheetList = res.sheetNameData;
            this.selectedExcelSheet = res.sheetNameData[0].sheetOrder;
            // 获取预览模板数据
            this.loadPreviewTplFile();
          });
      } else {
        // 获取预览模板数据
        this.loadPreviewTplFile();
      }
    },
    // 监听模板编辑-顶部报表位置选项变化，动态改变预览模板
    selectedExcelSheet(newVal, oldVal) {
      this.updateSheetOrder(newVal);
      // 判断oldVal是否为空，以防重复请求
      if (oldVal === 0 || oldVal) {
        this.loadPreviewTplFile();
      }
    }
  },
  components: {
    UpdateDialog,
    PreContent
  },
  methods: {
    // 改变vuex状态
    ...mapActions("fileModelConfig", [
      "updateSheetOrder",// 报表位置更新
      "showUpdateDialogVisible" // 显示更新模板对话框
    ]),
    // 预览-加载模板预览数据
    loadPreviewTplFile() {
      this.isTplPreviewLoading = true;
      this.$refs.initPreview.innerHTML = "";
      this.$nextTick(() => {
        // 获取数据
        this.$api.fileModelConfig
          .getPreviewTplFile({
            versionId: this.selectedTplVersion, // 选中模板id
            sheetOrder: this.selectedExcelSheet, // 选中报表位置id
            rowCode: this.rowCode, // 行id
            category: this.category // 模板版本类别
          })
          .then(res => {
            // 初始化
            this.$refs.initPreview.scrollTop = 0;
            /**
             * 动态编译html字符串
             * 将请求到的html字符串片段作为组件编译为DOM片段，插入DOM
             */
            let EditTpl = Compile({
              template: `${res.txtNoHandle.txt}`,
              data() {
                return {
                  style: res.txtNoHandle.style
                };
              }
            });
            this.$refs.initPreview.appendChild(EditTpl);
            this.isTplPreviewLoading = false;
          })
          .catch(err => {
            this.$message.error(err);
          });
      });
    },
    // 更新配置-显示更新配置对话框
    openUpdateDialog() {
      this.showUpdateDialogVisible();
    },
    // 预生成-显示预生成筛选对话框
    showPreFormDialog() {
      this.preDialogVisible = true;
      this.preTplVisible = false;
      this.$api.fileModelConfig
        .getPrvdAndAudTrm({
          subjectId: this.subjectId,
          category: this.category
        })
        .then(res => {
          this.prvdList = res.preGenPrvd;
          this.audTrmList = res.preGenAudTrm;
          if (res.preGenPrvd.length > 0) {
            this.preForm.prvdId = res.preGenPrvd[0].id || "";
          }
          if (res.preGenAudTrm.length > 0) {
            this.preForm.audTrm = res.preGenAudTrm[0].id || "";
          }
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // 预生成-显示预生成模板
    showPreTpl() {
      let postData = JSON.parse(JSON.stringify(this.preForm));
      postData.subjectId = this.subjectId;
      postData.sheetOrder = this.selectedExcelSheet;
      postData.versionId = this.selectedTplVersion;
      postData.category = this.category;
      postData.rowCode = this.rowCode;
      this.$api.fileModelConfig
        .getPreFileState(postData)
        .then(res => {
          if (res) {
            this.preTplVisible = true;
          } else {
            this.$message("预生成文件失败");
          }
        })
        .catch(err => {
          this.$message.error(err);
        });
    },
    // 导出配置文件
    exportConfigFile() {
      window.open(
        `${this.$baseUrl}/fileModelConfig/outputConf?rowCode=${
          this.rowCode
        }&versionId=${this.selectedTplVersion}&category=${this.category}`
      );
    },
    // 选择导入文件
    chooseFile() {
      this.importData = {
        rowCode: this.rowCode,
        versionId: this.selectedTplVersion,
        category: this.category
      };
    },
    // 导入对话框-上传状态-上传中
    uploading() {
      this.isUploading = true;
    },
    // 导入-上传状态-上传成功
    importSuccess(response, file, fileList) {
      switch (response) {
        case 1: // 成功
          this.$message({
            type: "success",
            message: "导入成功!"
          });
          break;
        case 2: // 导入文件不一致
          this.$alert("导入文件不一致", "导入失败", {
            confirmButtonText: "确定",
            callback: () => {
              this.isUploading = false;
            }
          });
          break;
        default:
          // 文件内容不匹配
          this.$alert("请检查配置文件内容", "导入失败", {
            confirmButtonText: "确定",
            callback: () => {
              this.isUploading = false;
            }
          });
          break;
      }
      this.isUploading = false;
    },
    // 导入-上传状态-上传失败
    importError() {
      this.$message({
        type: "error",
        message: "导入失败，请重新导入!"
      });
      this.isUploading = false;
    }
  },
  mounted() {
    // 初始化模板版本下拉选项(下拉选项数据)->默认选中项->选中版本预览文件展示
    this.$nextTick(() => {
      this.selectedTplVersion = this.editTplList.selected;
    });
  }
};
</script>

<style lang="scss" scoped>
.main-edit-content {
  padding: 20px;
  .upload {
    display: inline-block;
    margin-left: 10px;
  }
  .edit-form-container,
  .edit-review-container {
    height: 100%;
    overflow-y: auto;
  }
  .edit-form-container {
    padding: 0 10px 0 30px;
  }
  /deep/ .el-dialog {
    width: 90%;
    margin-top: 7vh !important;
  }
  .title-dot {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
  }
}
</style>

